/*
  初始化 dayjs 相关的配置
*/
import Vue from 'vue'
import dayjs from 'dayjs'
// 加载使用中文语言包
import 'dayjs/locale/zh-cn'

import relativeTime from 'dayjs/plugin/relativeTime'
// 配置使用相对时间
dayjs.extend(relativeTime)

dayjs.locale('zh-cn') // 全局使用

// 把处理相对时间的代码包装为全局过滤器
// 然后就可以在任何组件的模板中使用了
// 在组件中的模板中使用过滤器：{{ xxx | relativeTime}}
// 管道符前面的内容会作为参数传递给过滤器函数
// 过滤器的返回值会渲染到使用过滤器的模板中
Vue.filter('relativeTime', value => {
  return dayjs(value).from(dayjs())
})

// 时间格式
Vue.filter('dateTime', (value, format) => {
  return dayjs(value).format(format)
})
// 例如日期格式化
// console.log(dayjs().format('YYYY-MM-DD'))

// 距离 X 的相对时间 .from(compared: Dayjs, withoutSuffix?: boolean)
// 返回 string 距离 X 的相对时间

// console.log(dayjs('2021-08-01').from(dayjs()))
